.navbar{
    display: flex;
    justify-content: space-between;
    background-color: #ef5350;
    color: white;
    padding: 5px;
    box-shadow: #5a5757 1px 1px 8px;
}


.navlinks{
    display: flex;
    list-style: none;
    padding: 0%;
    margin: 0;
    column-gap: 20px;
    text-transform: capitalize;
}


.loginbutton{
    border: none;
    border-radius: 20px;
    width: 100px;
    font-weight: bolder;
    padding: 5px;
}